import type { Endpoints } from "@/types.ts";

export type AuthConfig = {
  clientId: string;
  clientSecret?: string; // Optional client secret for confidential clients
  pkce?: boolean; // Optional PKCE flag, defaults to true if not specified
  redirectUrl: string;
  oauthServer?: string;
  challengeUrl?: string;
  refreshUrl?: string;
  endpointOverrides?: Partial<Endpoints> | undefined;
  postLogoutRedirectUrl?: string;
};
